/* ## Responsive Navigation
--------------------------------------------- */
.menu-toggle,
.sub-menu-toggle {
  background-color: #fff;
  border-width: 0;
  color: #333;
  display: block;
  margin: 0 auto;
  overflow: hidden;
  text-align: center;
  visibility: visible;
}

.menu-toggle:focus, .menu-toggle:hover,
.sub-menu-toggle:focus,
.sub-menu-toggle:hover {
  background-color: #fff;
  border-width: 0;
  color: #2979ff;
}

@media (min-width: 1023px) {
  .menu-toggle,
  .sub-menu-toggle {
    display: none;
    visibility: hidden;
  }
}

.menu-toggle {
  line-height: 2.2rem;
  flex-basis: 10.0rem;
  position: relative;
  text-transform: uppercase;
  width: 100%;
  z-index: 1000;
}

.header-nav .menu-toggle {
  font-size: 1.4rem;
  margin-right: 0;
}

.header-nav.menu-overlay .menu-toggle {
  position: absolute;
  right: 1.0rem;
  width: auto;
}

.header-nav.menu-overlay .menu-toggle.activated {
  position: fixed;
}

.menu-toggle::before {
  font-size: 1.5em;
  margin-right: 1.0rem;
  text-rendering: auto;
}

.header-nav .menu-toggle::before {
  font-size: 2.4rem;
  margin-right: .5rem;
  position: absolute;
  right: 0;
  top: calc(50% - 1.2rem);
}

.sub-menu-toggle {
  float: right;
  padding: 1.3rem 1.0rem;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 100;
}

.sub-menu .sub-menu-toggle {
  padding: 1.2rem 1.0rem;
}

.sub-menu-toggle::before {
  display: inline-block;
  text-rendering: auto;
  -webkit-transform: rotate(0);
  -ms-transform: rotate(0);
  transform: rotate(0);
  -webkit-transition: transform .25s ease-in-out;
  -ms-transition: transform .25s ease-in-out;
  transition: transform .25s ease-in-out;
}

.sub-menu-toggle.activated::before {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.js .nav-primary {
  display: none;
  position: relative;
}

.js .nav-primary.activated {
  display: block;
}

@media (min-width: 1023px) {
  .js .nav-primary {
    display: block;
  }
}

.js .nav-primary .wrap {
  padding: 0;
}

.js .nav-primary nav.pagination {
  display: block;
}

.js .nav-primary .genesis-nav-menu .menu-item {
  display: block;
  float: none;
  position: relative;
  text-align: left;
}

@media (min-width: 1023px) {
  .js .nav-primary .genesis-nav-menu .menu-item {
    margin: 0;
    display: inline-block;
  }
}

.js .nav-primary .genesis-nav-menu .menu-item a {
  border: none;
  margin-bottom: .1rem;
  padding: 1.5rem 2.0rem;
  width: 100%;
}

@media (min-width: 1023px) {
  .js .nav-primary .genesis-nav-menu .menu-item a {
    margin-bottom: 0;
    padding: 2.4rem 2.0rem;
  }
}

.js .nav-primary .genesis-nav-menu .menu-item:hover {
  position: relative;
}

.js .nav-primary .genesis-nav-menu .menu-item > a:focus ul.sub-menu,
.js .nav-primary .genesis-nav-menu .menu-item > a:focus ul.sub-menu .sub-menu {
  left: 0;
  margin-left: 0;
}

.js .nav-primary .genesis-nav-menu .menu-item .sub-menu {
  border: none;
  clear: both;
  display: none;
  margin: 0;
  opacity: 1;
  padding-left: 1.5rem;
  position: static;
  width: 100%;
}

@media (min-width: 1023px) {
  .js .nav-primary .genesis-nav-menu .menu-item .sub-menu {
    border-top: .1rem solid #eee;
    padding-left: 0;
    position: absolute;
    width: 20.0rem;
  }
  .js .nav-primary .genesis-nav-menu .menu-item .sub-menu a {
    border: .1rem solid #eee;
    border-top: none;
    padding: 2.0rem;
    width: 20.0rem;
  }
}

.js .nav-primary .genesis-nav-menu .menu-item .sub-menu .sub-menu {
  margin: 0;
}

@media (min-width: 1023px) {
  .js .nav-primary .genesis-nav-menu .menu-item .sub-menu .sub-menu {
    margin: -5.6rem 0 0 19.9rem;
    padding-left: 0;
  }
}

.js .nav-primary .genesis-nav-menu > .menu-item-has-children > a::after {
  content: none;
}

.js .nav-primary nav button:hover,
.js .nav-primary .menu-toggle:hover, .js .nav-primary {
  background-color: #fff;
  color: #000;
}

.js .nav-primary nav button:focus,
.js .nav-primary .menu-toggle:focus {
  background-color: #fff;
  color: #2979ff;
}

.js .header-nav.menu-overlay .nav-primary {
  display: block;
  height: 100%;
  left: -99.9rem;
  padding: 8.0rem 1.0rem 6.0rem;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
  -webkit-transition: left .3s ease;
  transition: left .3s ease;
}

.js .header-nav.menu-overlay .nav-primary.activated {
  left: 0;
}

@media (min-width: 1023px) {
  .js .header-nav.menu-overlay .nav-primary {
    height: auto;
    padding: 0;
    position: static;
  }
}

/* ## Navigation Menu
--------------------------------------------- */
.genesis-nav-menu {
  clear: both;
  font-size: 1.6rem;
  font-weight: 600;
  line-height: 1;
}

.genesis-nav-menu .menu-item {
  display: inline-block;
  margin-bottom: 0;
  padding-bottom: 0;
  text-align: left;
}

.genesis-nav-menu a {
  color: #333;
  text-decoration: none;
  display: block;
  padding: 3.2rem 2.0rem;
}

.genesis-nav-menu a:hover,
.genesis-nav-menu a:focus,
.genesis-nav-menu .current-menu-item > a,
.genesis-nav-menu .sub-menu .current-menu-item > a:hover,
.genesis-nav-menu .sub-menu .current-menu-item > a:focus {
  color: #2979ff;
  text-decoration: underline;
}

.genesis-nav-menu .sub-menu {
  border-top: .1rem solid #eee;
  left: -999.9rem;
  opacity: 0;
  position: absolute;
  -webkit-transition: opacity .4s ease-in-out;
  -moz-transition: opacity .4s ease-in-out;
  -ms-transition: opacity .4s ease-in-out;
  -o-transition: opacity .4s ease-in-out;
  transition: opacity .4s ease-in-out;
  width: 20.0rem;
  z-index: 99;
}

.genesis-nav-menu .sub-menu a {
  background-color: #fff;
  border: .1rem solid #eee;
  border-top: 0;
  font-size: 1.4rem;
  padding: 2.0rem;
  position: relative;
  width: 20.0rem;
  word-wrap: break-word;
}

.genesis-nav-menu .sub-menu .sub-menu {
  margin: -5.6rem 0 0 19.9rem;
  padding-left: 0;
}

.genesis-nav-menu .menu-item:hover {
  position: static;
}

.genesis-nav-menu .menu-item:hover > .sub-menu {
  left: auto;
  opacity: 1;
}

.genesis-nav-menu > .first > a {
